import React, { useRef, useState, useEffect } from "react";
import { useAuth0 } from "@auth0/auth0-react";
import { Link, useLocation } from "react-router-dom";
import "../App.css";
import "../components/NavBar.css";
import "clarity-ui/clarity-ui.min.css";
import "clarity-icons/clarity-icons.min.css";
import "clarity-icons/shapes/technology-shapes.js";
import "@cds/core/button/register.js";
import "@cds/core/icon/register.js";
import "@cds/core/dropdown/register.js";
import "@cds/core/divider/register.js";
import {
  ClarityIcons,
  userIcon,
  bellIcon,
  cogIcon,
  homeIcon,
  vmIcon,
  searchIcon,
  thumbsUpIcon,
} from "@cds/core/icon";
import "@webcomponents/custom-elements/custom-elements.min.js";
import "@clr/icons/clr-icons.min.css";
import "@clr/icons/shapes/technology-shapes.js";
import ModalAuth from "./modal_auth.js";
import Dropdown1 from "./dropdown_1.jsx";
import Dropdown2 from "./dropdown_2.jsx";
import Dropdown3 from "./dropdown_3.jsx";

ClarityIcons.addIcons(bellIcon);
ClarityIcons.addIcons(cogIcon);
ClarityIcons.addIcons(userIcon);
ClarityIcons.addIcons(vmIcon);
ClarityIcons.addIcons(homeIcon);
ClarityIcons.addIcons(searchIcon);
ClarityIcons.addIcons(thumbsUpIcon);

function NavBar() {
  const { isLoading, user, getAccessTokenSilently } = useAuth0();

  const [searchQuery, setSearchQuery] = useState('');
  const [showLogo, setShowLogo] = useState(true);
  const [roles, setRoles] = useState([]);
  const location = useLocation();
  const email = user ? user.email : "Guest";
  

  useEffect(() => {
    if (!(location.pathname === '/')) {
      setShowLogo(false);
    } else {
      setShowLogo(true);
    }
  }, [location]);

  useEffect(() => {
    const fetchRoles = async () => {
      if (user) {
        try {
          const token = await getAccessTokenSilently({
            audience: process.env.REACT_APP_AUTH0_AUDIENCE,
            scope: 'read:roles read:users',
          });
          const response = await fetch(`https://${process.env.REACT_APP_AUTH0_DOMAIN}/api/v2/users/${user.sub}/roles`, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });
          if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
          const data = await response.json();
          setRoles(data);
          console.log('Fetched roles:', data);
        } catch (error) {
          console.error('Error fetching roles:', error);
        }
      }
    };

    fetchRoles();
  }, [user, getAccessTokenSilently]);

  const checkboxRef = useRef(null);

  const handleCheckboxChange = () => {
    const isDarkMode = document.body.classList.toggle("dark");
    localStorage.setItem("darkMode", isDarkMode ? "enabled" : "disabled");
  };

  useEffect(() => {
    const darkMode = localStorage.getItem("darkMode");
    if (darkMode === "enabled") {
      document.body.classList.add("dark");
    }
  }, []);

  const handleSearchInputChange = (event) => {
    setSearchQuery(event.target.value);
  };

  const handleSearch = (event) => {
    event.preventDefault();
    console.log('Searching for:', searchQuery);
  };

  return (
    <header className="App-header">
      <div className="main">
        <header className="header header-6">
          <div className="branding">
            <Link to="/" className="title">MSTE</Link>
          </div>
          <form className="search" onSubmit={handleSearch}>
            <input
              id="search-input-sidenav-ng"
              type="text"
              placeholder="Search for keywords..."
              value={searchQuery}
              onChange={handleSearchInputChange}
            />
            <button type="submit" className="nav-link nav-icon">
              <cds-icon shape="search">
                Search
              </cds-icon>
            </button>
          </form>
          {!isLoading && user &&(
            <div className="currentUser">
              <cds-icon shape="thumbs-up"></cds-icon>
              <span className="display_email">
                Logged in as {email || "Guest"}
                </span>
                {console.log('Logged with role:', roles)}
            </div>
          )}
          <ModalAuth isLoggedIn={isLoading} />
          <div className="settings">
            <input
              type="checkbox"
              className="checkbox"
              id="checkbox"
              ref={checkboxRef}
              onChange={handleCheckboxChange}
            />
            <label htmlFor="checkbox" className="checkbox-label">
              <i className="fas fa-moon"></i>
              <i className="fas fa-sun"></i>
              <div className="ball"></div>
            </label>
            <Link to="/" className="nav-link nav-icon">
              <cds-icon shape="home"></cds-icon>
            </Link>
            {/* {!isLoading && user &&( */}
              <Link to="/auth-settings" className="nav-link nav-icon">
                <cds-icon shape="cog"></cds-icon>
              </Link>
            {/* )} */}
          </div>
        </header>
        <nav className="subnav">
          <ul className="nav">
            <Dropdown1 />
            <Dropdown2 />
            <Dropdown3 isLoggedIn={!isLoading} />
            <Link to="/links">LINKS</Link>
            <Link to="/partners">PARTNERS</Link>
            <Link to="/contacts">CONTACTS</Link>
          </ul>
          {showLogo && (
            <img
              className="logo"
              src="https://res.cloudinary.com/djunroohl/image/upload/v1726651079/Untitled_zwoq69.svg"
              alt="logo"
            />
          )}
        </nav>
      </div>
    </header>
  );
}

export default NavBar;